
let tg = window.Telegram.WebApp;

export function useTelegram() {

    const onClose = () => {
        tg.showPopup({
            title: " ",
            message: "    ?    !",
            buttons: [
                {
                    id: "1",
                    text: ''
                },
                {
                    id: "close",
                    text: ''
                }
            ]
        }, (id) => {
            if (id === "close") {

                tg.close()
            }
        })

    }

    const onToggleButton = () => {
        const mainBtn = tg.MainButton;

        if (mainBtn.isVisible) {
            mainBtn.hide()
        } else {
            mainBtn.show();
        }
    }

    let mainBtn = tg.MainButton;
    return {onClose, tg, mainBtn}
}